.footer {
  overflow: hidden;
  /* background-color: #f8f9fa; */
  background-color: #06283d;
}

.footer .background_drawings img {
  z-index: 1;
  position: relative;
  max-width: 100%;
  max-height: 100%;
  opacity: 0.15;
}

.footer .background_drawings div {
  /* background-color: rgb(194, 194, 194); */
  position: absolute;
}

.footer .drawing_1 {
  left: 0%;
  top: 0vh;
  width: 11vh;
  height: 11vh;
}

.footer .drawing_2 {
  left: 50%;
  top: 3.5vh;
  width: 7vh;
  height: 7vh;
}

.footer .drawing_3 {
  right: 5%;
  top: 7vh;
  width: 11vh;
  height: 11vh;
}

.footer .drawing_4 {
  left: 10%;
  top: 16vh;
  width: 7vh;
  height: 7vh;
}

.footer .drawing_5 {
  left: 35%;
  top: 19vh;
  width: 11vh;
  height: 11vh;
}

.footer .drawing_6 {
  right: 15%;
  top: 23vh;
  width: 7vh;
  height: 7vh;
}

