.icons-list {
  z-index: 2;
  list-style: none;
  position: absolute;
  top: 10vh;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: nowrap;
}

.icons-list li {
  float: left;
}

.footer .fa-icon {
  margin: 1.5vh;
  color: whitesmoke;
  /* color: rgb(70, 68, 68); */
  filter: drop-shadow(0 0 0.3vh rgb(0, 0, 0));
  font-size: 3.5vh !important;
}

/* .fa-icon:hover,
.fa-icon:active {
  font-size: 5vh;
} */

.footer-comment {
  z-index: 2;
  white-space: nowrap;
  color: whitesmoke;
  text-shadow: 0 0 0.5vh rgb(0, 0, 0);
  /* color: rgb(70, 68, 68); */
  position: absolute;
  top: 25vh;
  font-weight: 500;
  font-size: 1.5vh;
  left: 50%;
  transform: translateX(-50%);
}

.react-logo {
  z-index: 2;
  vertical-align: middle;
  height: 2.5vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    animation: react-logo-spin infinite 20s linear;
  }
}

@keyframes react-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
