.header {
  overflow: hidden;
  background-color: #304a62;
}

.language_tag {
  z-index: 2;
  /* color: white; */
  color: whitesmoke;
  text-shadow: 0 0 0.5vh rgb(0, 0, 0);
  /* color: rgb(70, 68, 68); */
  position: absolute;
  top: 3%;
  right: 5%;
  font-size: 1.5vh;
  font-weight: 600;
}

.language_tag a {
  cursor: pointer;
}

