.image {
  z-index: 2;
  position: absolute;
  top: 6vh;
  left: 50%;
  /* transform: translateY(-9vh); */
  transform: translateX(-50%);
  /* zoom: 130%; */
  height: 18vh;
  border-radius: 100%;
  display: block;
}

