.presentation_text {
  z-index: 2;
  position: relative;
  top: 25vh;
  /* color: rgb(255, 255, 255); */
  color: whitesmoke;
  /* color: rgb(70, 68, 68); */
  text-shadow: 0 0 1vh rgb(0, 0, 0);
  font-size: 3vh;
}

.presentation_text strong {
  font-size: 4vh;
}

.links-list {
  z-index: 2;
  margin: 0px;
  list-style: none;
  position: absolute;
  bottom: 10vh;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: nowrap;
}

.links-list li {
  margin-left: 2vh;
  margin-right: 2vh;
  font-size: 2.7vh;
  font-weight: 600;
}

.links-list li a:link,
.links-list li a:visited,
.links-list li a:hover,
.links-list li a:active {
  text-decoration: none;
  /* color: white; */
  color: whitesmoke;
  text-shadow: 0 0 0.5vh rgb(0, 0, 0);
  /* color: rgb(70, 68, 68); */
}
