.certificates {
  background-color: #3d556d;
  display: flex;
  text-align: left;
  font-size: 1.5rem;
  font-weight: 500;
  overflow: hidden;
}

.certificates-container {
  margin: 0px;
  padding: 4% 3% 0% 3%;
  max-width: 100%;
  min-height: 100.1vh;
}

.certificates-row {
  margin: 0px;
}

.certificates-column {
  padding: 0% 1% 4.16% 1%;
}

.certificates-groupset {
  background-color: #f8f9fa;
  width: 100%;
  height: 100%;
  border: 1px #3d556d;
  box-shadow: 0px 0px 1.2vw -0.6vw #f8f9fa;
}

.certificates-groupset h1 {
  display: flex;
  justify-content: left;
  align-items: center;
  background-color: #304a62;
  color: white;
  padding: 3% 2% 3% 2%;
  text-shadow: 0 0 0.75rem rgb(0, 0, 0);
  font-size: 2rem;
  height: 7rem;
  margin: 0px;
}

.certificates-groupset ul {
  color: black;
  padding: 2% 2% 1% 2%;
  margin: 0px;
  list-style: none;
}

.certificates-groupset li {
  padding: 1% 0% 1% 0%;
}
