.awards-new {
  background-color: #f3f2ef;
}

.swiper-container {
  margin: 20vh 0% 20vh 0%;
  width: 100%;
  height: 60vh;
  overflow: visible;
}

.swiper-slide {
  border: 1px solid #e0dfdc;
  border-radius: 20px;
  background-color: #ffffff;
}

.swiper-slide-shadow-left,
.swiper-slide-shadow-right {
  border-radius: 20px;
}

.swiper-slide-caption {
  color: black;
  max-height: 100%;
  padding: 4% 2% 0% 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.awards-icon {
  width: min(11vh, 6rem);
}

.swiper-slide-caption h1 {
  font-size: min(4.58vh, 2.5rem);
}

.swiper-slide-caption h2 {
  font-size: min(3.66vh, 2rem);
}

.swiper-slide-caption p {
  font-weight: 400;
  font-size: min(3.3vh, 1.8rem);
  padding: 5% 0% 5% 0%;
}

.swiper-slide-caption h3 {
  font-weight: 400;
  font-size: min(2.75vh, 1.5rem);
}

.swiper-slide-caption h1.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.certificate-img {
  padding-top: 2%;
  max-width: 100%;
  max-height: calc(35vh - 20rem);
}

@media screen and (max-width: 1200px) {
  .swiper-slide-caption p {
    padding: 1% 0% 1% 0%;
  }
  .swiper-container {
    margin: 5vh 5% 5vh 5%;
    width: 90%;
  }
}
